<template>
	<view class="container">
		<view class="uni-list">
			<uni-section title="圆头像且不显示分割线" type="line"></uni-section>
			<uni-list :border="false">
				<uni-list-chat v-for="item in listData" :avatar-circle="true" :key="item.id" :title="item.memberUserName" :avatar="item.memberUserAvatar" :note="item.content" :time="item.create_time" :clickable="false"></uni-list-chat>
			</uni-list>
		 </view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				listData:[]
			}
		},
		onLoad:function(){
			this.loadChatList();
		},
		onTabItemTap:function(){
			this.loadChatList();
		},
		methods: {
			openInfo(e) {
				console.log(e)
				var newsId = e.currentTarget.dataset.newsid;
				uni.navigateTo({
					url: '../info/info?newsId='+newsId,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			loadChatList() {
				uni.showLoading({})
				uni.request({
					// url: 'https://unidemo.dcloud.net.cn/api/news',
					url: 'http://localhost:8080/chatContentInfo/selectPage',
					method: 'POST',
					data: {
						primaryUserId:1,
						pageNum: 1,
						pageSize:4
					},
					success: res => {
						console.log(res);
						
						let result = res.data;
						if (result.code == 0){
							this.listData = result.result.list;
							console.log(this.listData);
						}
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();	
					}
				});
			}
		}
	}
</script>

<style>
	.container {
		padding: 20px;
		font-size: 14px;
		line-height: 24px;
	}
	.uni-media-list-body {
		height: auto;
	}
	.uni-media-list-text-top{
		line-height: 1.6em;
	}
</style>
